
  
  .timeline {
    width: 100%;
    /* max-width: 800px; */
    /* background: var(--clr-fg-alt); */
    padding: 100px 50px;
    position: relative;
    /* box-shadow: 0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2); */
  }
  .timeline:before {
    content: '';
    position: absolute;
    top: 0px;
    left: calc(33% + 15px);
    bottom: 0px;
    width: 4px;
    background: #ddd;
  }
  
  .timeline:after {
    content: '';
    display: table;
    clear: both;
  }
  
  .entry {
    clear: both;
    text-align: left;
    position: relative;
  }
  
  .entry .title {
    margin-bottom: 0.5em;
    float: left;
    width: 33%;
    padding-right: 30px;
    text-align: right;
    position: relative;
    background-color: transparent;
  }
  
  .entry .title:before {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    border: 4px solid salmon;
    background-color: #fff;
    border-radius: 100%;
    top: 15%;
    right: -8px;
    z-index: 99;
  }
  
  .entry .title h3 {
    margin: 0;
    font-size: 120%;
  }
  
  .entry .title p {
    margin: 0;
    font-size: 100%;
  }
  
  .entry .body {
    margin: 0 0 3em;
    float: right;
    width: 66%;
    padding-left: 30px;
    text-align: justify;
  }
  
  .entry .body p {
    line-height: 1.4em;
    padding-top: 20px;
  }
  
  .entry .body p:first-child {
    margin-top: 0;
    font-weight: 400;
  }
  
  .entry .body ul {
    color: #aaa;
    padding-left: 0;
    list-style-type: none;
  }
  
  .entry .body ul li:before {
    content: '–';
    margin-right: 0.5em;
  }
  